import projectConfig from "projectConfig"
import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import { MDXRenderer } from "gatsby-plugin-mdx"
import Layout from "globalLayoutsShopProduct"
import { Cart3 } from "react-bootstrap-icons"
import Row from "globalAtoms/Row"
import ElementList from "globalOrganisms/ElementList"
import BuyModal from "globalOrganisms/buyModal"
import * as helpers from "globalLibs/helpers"
import * as S from "./ShopProduct/styles"

export default function ShopProduct(props) {
  const node = props.data.mdx
  const frontmatter = props.data.mdx.frontmatter
  const extra = props.data.mdx.frontmatter.extra

  function getProductDescription() {
    if (node.excerpt !== null) return <MDXRenderer>{node.body}</MDXRenderer>
  }

  function getProductWarrantyText() {
    const categoryInfo = helpers.getCategoryInfo(frontmatter.categories[0])
    if (categoryInfo !== undefined && categoryInfo.warrantyText) {
      return <span>{categoryInfo.warrantyText}</span>
    } else {
      return <span>{projectConfig.environment.defaultProductWarrantyText}</span>
    }
  }

  function renderProductCharacteristics() {
    return (
      <>
        {extra.server !== null && (
          <Row className={"element-medium"}>
            <span className={"left-part px-3"}>Сервер</span>
            <span className={"right-part px-3"}>{extra.server}</span>
          </Row>
        )}
        {extra.phone !== null && (
          <Row className={"element-medium"}>
            <span className={"left-part px-3"}>
              Привязка аккаунта и почты к телефону
            </span>
            <span className={"right-part px-3"}>
              {extra.phone ? "Есть" : "Нет"}
            </span>
          </Row>
        )}
        {extra.mail !== null && (
          <Row className={"element-medium"}>
            <span className={"left-part px-3"}>Привязка аккаунта к почте</span>
            <span className={"right-part px-3"}>
              {extra.mail ? "Есть" : "Нет"}
            </span>
          </Row>
        )}
        {extra.vehicles.x !== null && (
          <Row className={"element-medium"}>
            <span className={"left-part px-3"}>ТОПы</span>
            {extra.vehicles.x.map(element => (
              <span>
                <a
                  key={element}
                  target={"blank"}
                  href={`https://wiki.wargaming.net/ru/index.php?search=${element}`}
                >
                  {element}
                </a>
              </span>
            ))}
          </Row>
        )}
        {extra.vehicles.premium !== null && (
          <Row className={"element-medium"}>
            <span className={"left-part px-3"}>Премиум техника</span>
            <span className={"right-part px-3"}>
              {extra.vehicles.premium.map(element => (
                <a
                  key={element}
                  target={"blank"}
                  href={`https://wiki.wargaming.net/ru/index.php?search=${element}`}
                  className={"color-blue"}
                >
                  {element}
                </a>
              ))}
            </span>
          </Row>
        )}
      </>
    )
  }

  return (
    <>
      <GatsbySeo
        title={`Купить ${frontmatter.title} | Магазин World of Tanks`}
        description={node.excerpt}
        canonical={`${props.location.href}`}
        openGraph={{
          url: `${props.location.href}`,
          title: `Купить ${frontmatter.title} | Магазин World of Tanks`,
          description: `${node.excerpt}`,
        }}
      />
      <Layout className={"adaptive bg-primary-to-secondary"}>
        <S.ProductCard>
          <h1 className={"large-title__large product-title adaptive text"}>
            {frontmatter.title}
          </h1>
          <div className={"product-tab-main"}>
            <div className={"product-about"}>
              <div className={"left-part"}>
                <div className={"product-photo"}>
                  <Img
                    alt={frontmatter.title}
                    title={frontmatter.title}
                    fluid={frontmatter.cover.childImageSharp.fluid}
                    className={"adaptive rounded"}
                    style={{ maxWidth: "600px", width: "100%" }}
                  />
                </div>
              </div>
              <div className={"right-part"}>
                <div className={"product-main-info"}>
                  <div
                    className={
                      "product-price_block adaptive rounded border-outset p-3"
                    }
                  >
                    <span
                      className={"h1__large me-4"}
                    >{`${extra.price} ₽`}</span>
                    <button
                      aria-label="product-buy-button"
                      data-bs-toggle="modal"
                      data-bs-target={`#${node.id.replace(
                        /[.,"*+!-?^${}()|[\]\\/]/gu,
                        ""
                      )}`}
                      className="element-large element-button green product-buy-button"
                    >
                      <i>
                        <Cart3 size={20} />
                      </i>
                      Купить
                    </button>
                    <BuyModal data={node}></BuyModal>
                  </div>
                  <ElementList
                    title={"Доставка"}
                    listClassName={
                      "adaptive rounded border-outset subhead__large border-top p-3"
                    }
                  >
                    После оплаты товар будет отправлен на почту, указанную при
                    оформлении заказа
                  </ElementList>
                  <ElementList
                    title={"Оплата"}
                    listClassName={
                      "adaptive rounded border-outset subhead__large border-top p-3"
                    }
                  >
                    Банковская карта или Qiwi
                  </ElementList>
                  <ElementList
                    title={"Гарантия"}
                    listClassName={
                      "adaptive rounded border-outset subhead__large border-top p-3"
                    }
                  >
                    {getProductWarrantyText()}
                  </ElementList>
                </div>
              </div>
            </div>
            <div className={"product-about"}>
              <div className={"left-part"}>
                <h2 className={"adaptive text mb-3"}>
                  Описание
                  <span
                    className={"text_secondary"}
                  >{` ${frontmatter.title}`}</span>
                </h2>
                <div className={"text_tertiary adaptive text"}>
                  {getProductDescription()}
                </div>
              </div>
              <div className={"right-part"}>
                <h2 className={"adaptive text mb-3"}>
                  Характеристики
                  <span
                    className={"text_secondary"}
                  >{` ${frontmatter.title}`}</span>
                </h2>
                <div className={"text_tertiary"}>
                  <ElementList listClassName={"adaptive rounded border-outset"}>
                    {renderProductCharacteristics()}
                  </ElementList>
                </div>
              </div>
            </div>
          </div>
        </S.ProductCard>
      </Layout>
    </>
  )
}

export const query = graphql`
  query($slug: String!) {
    mdx(slug: { eq: $slug }) {
      body
      excerpt
      frontmatter {
        title
        slug
        categories
        cover {
          childImageSharp {
            fluid(maxWidth: 600, quality: 100) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        extra {
          amount
          price
          link
          phone
          mail
          server
          vehicles {
            x
            premium
          }
        }
      }
      id
    }
  }
`
