import projectConfig from "projectConfig"
import styled from "styled-components"
const { styleVariables: siteStyleVariables } = projectConfig
const { root } = siteStyleVariables

export const ProductCard = styled.section`
  display: grid;
  .product-photo {
    display: flex;
    justify-content: center;
    /* & img {
      border-radius: ${root.secondaryBorderRadius};
    } */
  }

  & .product-title {
    margin-bottom: 32px;
  }
  & .product-about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 32px;
    margin-bottom: 64px;
    @media (max-width: 992px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  & .product-price_block {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    background-color: ${({ theme }) => theme.background_primary};
  }
`
